<template>
  <div>
    <ul :class="tabsContailerStyle">
      <li
        v-for="(tab, index) in tabs"
        :key="index"
        :class="tab.isActive ? classes.tabNavItemActive : classes.tabNavItem"
        :aria-selected="tab.isActive"
        @click="setTab(tab)"
      >
        {{ tab.title }}
      </li>
    </ul>
    <slot />
  </div>
</template>

<script>
import SwTabs from '../../themes/default/SwTab'
import { installComponent } from '../../helpers/utilities'
const { classes } = SwTabs

export default {
  name: 'SwTabs',
  install(Vue, theme) {
    installComponent(Vue, theme, this)
  },
  props: {
    classes: {
      type: Object,
      default: () => classes
    },
    activeTab: {
      type: String,
      default: null
    }
  },
  data() {
    return {
      tabs: [],
      currentTab: null
    }
  },
  computed: {
    tabsContailerStyle() {
      return [this.classes.tabNavContainer]
    }
  },
  watch: {
    currentTab(newState, oldState) {
      this.tabs.map(tab => (tab.isActive = tab == newState))
      if (oldState != null) {
        this.$emit('update', newState)
      }
    },
    activeTab(newTitle) {
      if (newTitle) {
        this.setActiveTab()
      }
    }
  },
  mounted() {
    this.tabs = this.$children
    this.setActiveTab()
  },
  methods: {
    setTab(tab) {
      this.currentTab = tab

      if (tab && tab.route) {
        this.$router.push(tab.route)
      }
    },
    setActiveTab() {
      this.currentTab = this.activeTab
        ? this.tabs.find(tab => tab.title == this.activeTab)
        : this.tabs[0]
      if (this.currentTab.route) {
        this.$router.push(this.currentTab.route)
      }
    }
  }
}
</script>
